.html-player {
    padding: .3em .7em;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.player {
    height: 4.875rem;
    display: inline-block;
    -webkit-box-shadow: 0 0 1px 0 #000000;
            box-shadow: 0 0 1px 0 #000000;
}

.album {
    width: 4.875rem;
    height: 4.875rem;
    display: inline-block;
}

.mask {
    position: relative;
    background: rgba(0,0,0, .3);
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.btn {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 2.25rem;
    color: #efe8e8;
    margin-top: -5px;
}

.pause-btn {
    margin-top: -2px;
}

.song-info {
    position: relative;
    display: inline-block;
    width: 15.625rem;
    height: 100%;
    vertical-align: top;
}
.name {
    margin: .3em .8em;
}

.name>span:nth-child(3) {
    font-size: 0.75rem;
    color: #ac9b9b;
    font-style: italic;
}

.time-passed {
    margin: .5em 0 0 .8em;
    color: #797676;
}
.play-track {
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
}

.progress {
    width: 100%;
    height: 100%;
    background: red;
    -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
            transform-origin: 0 0;
    /* transition: transform .5s linear; */
}

@media (max-width: 400px) {
    .song-info {
        width: 14rem;
    }
}


